.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  .modalWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px 20px 20px 20px;
    border-radius: 15px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    .close {
      cursor: pointer;
      align-self: flex-end;
    }
    form {
      margin: 10px 0;
      .adresWrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 0 15px;
      }
      .full {
        grid-column: 1/3;
      }
      input {
        width: 100%;
        height: 30px;
        border: none;
        outline: 1px solid #b6b6b6;
        border-radius: 5px;
        padding-left: 10px;
      }
      label {
        display: block;
        margin-bottom: 15px;
        position: relative;
        &:last-child {
          margin-bottom: 0;
        }
        .label {
          display: block;
          margin-bottom: 5px;
          min-width: 150px;
        }
        .required {
          color: red;
        }
        .uncorrect {
          position: absolute;
          bottom: 0;
          left: 0;
          color: red;
          transform: translateY(120%);
          margin-bottom: 0;
          font-size: 12px;
        }
      }
    }
  }
}
.order {
  ul {
    li {
      margin-bottom: 5px;
      &:first-child {
        margin-top: 10px;
      }
      &:last-child {
        margin-bottom: 10px;
      }
    }
  }
}
.succes {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}
@media (max-width: 500px) {
  .modal {
    position: absolute;
    background-color: #fff;
    .modalWrapper {
      position: fixed;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      transform: none;
      border-radius: 0;
      form {
        label {
          .label {
            min-width: auto;
          }
        }
      }
    }
  }
}
@media (max-height: 500px) {
  .modal {
    position: absolute;
    .modalWrapper {
      width: 100%;
      min-height: 100%;
      left: 0;
      top: 0;
      transform: none;
      border-radius: 0;
      form {
        .adresWrapper {
          display: grid;
          grid-template-columns: 1fr 1fr 1fr 1fr;
          gap: 0 15px;
        }
        .full {
          grid-column: 1/5;
        }
        label {
          .label {
            min-width: auto;
          }
        }
      }
    }
  }
}
