.button {
  padding: 12px 15px;
  border: 2px solid currentColor;
  background-color: #fff;
  border-radius: 25px;
  font-weight: 700;
  color: #eb5a1e;
  cursor: pointer;
  &:hover {
    background-color: #fe5f1e;
    color: white;
  }
}
.grey {
  color: #b6b6b6;
  &:hover {
    color: black;
    background-color: transparent;
  }
}
.disabled {
  pointer-events: none;
  opacity: 0.5;
}
