.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 991px) {
    flex-direction: column;
    gap: 30px;
  }

  .categories {
    display: flex;
    gap: 20px;
    @media (max-width: 767px) {
    }
    @media (max-width: 575px) {
      font-size: 0.75rem;
    }
    .category {
      display: flex;
      align-items: center;
      padding: 0 20px;
      height: 46px;
      background: #f9f9f9;
      border-radius: 30px;
      font-weight: 700;
      cursor: pointer;
      @media (max-width: 767px) {
        border-radius: 20px;
      }
      @media (max-width: 575px) {
        padding: 0 10px;
        height: 36px;
        border-radius: 10px;
      }
    }
    .active {
      background: black;
      color: white;
    }
  }

  .sortByCriterion {
    position: relative;
    width: 205px;
    .chosenSort {
      text-decoration: underline;
      color: #fe5f1e;
      cursor: pointer;
    }
    .popup {
      top: 30px;
      left: 50px;
      position: absolute;
      padding: 5px 10px;
      border-radius: 10px;
      border: 2px solid;
      background-color: rgb(240, 240, 240);
      .popupElem {
        display: flex;
        width: 130px;
        height: 35px;
        align-items: center;
        cursor: pointer;
      }
      .active {
        background: rgba(254, 95, 30, 0.05);
        color: #fe5f1e;
      }
    }
  }
}
